.card-list {
  width: 85vw;
  margin: 0, auto;
  margin-left: 120px;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

@media only screen and (max-width: 600px) {
  .card-list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin-left: 40px;
  }
}
